function Rbtdel() {}

export default function RedBlackTree() {
  return (
    <div className="center">
      <div>
        <h1 className="test">Piros-Fekete Fa</h1>
        <br />

        <div className="lowpad">
          <a>Elem megadása : </a>
          <input type="text" id="addnumber" className="inputlong"></input>
          <a> </a>
          <a>Elem törlése : </a>
          <input type="text" id="" className="allinput"></input>
        </div>
        <div className="lowpad">
          <Button
            variant="warning"
            className="button1"
            id="btnadd"
            onClick={Rbtadd}
          >
            Megad
          </Button>
          <Button
            variant="warning"
            className="button1"
            id="btndel"
            onClick={Rbtdel}
          >
            Töröl
          </Button>
          <br />
          <br />
        </div>

        <div className="normalpad">
          <canvas id="tree" width={"450px"} height={"450px"}></canvas>
        </div>
      </div>
    </div>
  );
}
